export default {
  name: 'registerWrapper',
  data (){
    return{
      type: 'password',
      readonly: true,
      autofocus: true,
      autocomplete: true,
      eye: {
        open: false,
        reverse: false
      },
      inputName: '',
      inputPwd: '',
      inputPwds: '',
      inputTell: '',
      inputCode: '',
      captchaLabel:"获取验证码",
      seconds:60,
      captchaDisable:false,
      timer:null,
      isLoading: false,
      name: '',
      password: '',
      clearable: {
        visible: true,
        blurHidden: false
      },
      options: [1, 2, 3, 4, 5, 6],
      value: '',
      title: '请选择单位部门',
      placeholder: '设置密码',
      autoPop: false,
      disabled: false
    }
  },
  methods: {
    showToastTxtOnly(text) {
      this.toast = this.$createToast({
        txt: text,
        type: 'txt'
      })
      this.toast.show()
    },
    //定义倒计时数据
    countdown () {
      if (this.seconds < 1) {
        this.captchaLabel = '获取验证码';
      } else {
        this.captchaDisable = true;
        this.seconds --;
        this.captchaLabel = this.seconds + '秒后重新获取'
      }
    },
  },
  mounted (){
    this.$store.commit('changeLoading',false)
  }
}



